<template>
  <el-collapse v-model="activeNames" @change="handleChange">
    <el-collapse-item title="作者" name="3">
      <div style="cursor:pointer;color:#87CEFA" v-for="(value, key) in filter.author" :key="key" @click="Filter({keyword:value[0],type:'作者'})">{{value[0]}}({{value[1]}})</div>
    </el-collapse-item>
    <el-collapse-item title="出版社" name="4">
      <div style="cursor:pointer;color:#87CEFA" v-for="(value, key) in filter.press" :key="key" @click="Filter({keyword:value[0],type:'出版社'})">{{value[0]}}({{value[1]}})</div>
    </el-collapse-item>
    <el-collapse-item title="主题" name="5">
      <div style="cursor:pointer;color:#87CEFA" v-for="(value, key) in filter.category" :key="key" @click="Filter({keyword:value[0],type:'主题'})">{{value[0]}}({{value[1]}})</div>
    </el-collapse-item>
    <el-collapse-item title="语言" name="6">
      <div style="cursor:pointer;color:#87CEFA" v-for="(value, key) in filter.language" :key="key" @click="Filter({keyword:value[0],type:'语言'})">{{value[0]}}({{value[1]}})</div>
    </el-collapse-item>
  </el-collapse>
</template>

<script>
import axios from 'axios';
export default {
  name: "Aside",
  props: ['tableData'],
  data() {
    return {
      filter:{
        holdingPosition:undefined,
        author:undefined,
        press:undefined,
        category:undefined,
        language:undefined
      }
    }
  },
  methods:{
    Filter(value){
      this.$emit("asideFilter",value)
    },
    reloadFilter(){
      this.filter.holdingPosition = new Map();
      this.filter.author = new Map();
      this.filter.press = new Map();
      this.filter.category = new Map();
      this.filter.language = new Map();
      this.tableData.forEach(element=>{
        if(this.filter.holdingPosition.has(element.holdingPosition) == false)
          this.filter.holdingPosition.set(element.holdingPosition,1)
        else
          this.filter.holdingPosition.set(element.holdingPosition,this.filter.holdingPosition.get(element.holdingPosition) + 1);
        if(this.filter.author.has(element.author) == false)
          this.filter.author.set(element.author,1)
        else
          this.filter.author.set(element.author,this.filter.author.get(element.author) + 1);
        if(this.filter.press.has(element.press) == false)
          this.filter.press.set(element.press,1)
        else
          this.filter.press.set(element.press,this.filter.press.get(element.press) + 1);
        if(this.filter.category.has(element.category) == false)
          this.filter.category.set(element.category,1)
        else
          this.filter.category.set(element.category,this.filter.category.get(element.category) + 1);
        if(this.filter.language.has(element.language) == false)
          this.filter.language.set(element.language,1)
        else
          this.filter.language.set(element.language,this.filter.language.get(element.language) + 1);
      })
    }
  },
  created(){
    this.filter.holdingPosition = new Map();
    this.filter.author = new Map();
    this.filter.press = new Map();
    this.filter.category = new Map();
    this.filter.language = new Map();
  },
  watch:{
    tableData(newTableData,oldTableData)
    {
      this.reloadFilter();
    }
  }
};
</script>

