<template>
  <el-row>
    <el-col :span="3">
      <el-select v-if="show" v-model="normalSearchData.value" size="large">
        <el-option 
          v-for="item in normalSearchData.options"
          :key="item.value"
          :label="item.label"
          :value="item.value"
        />
      </el-select>
    </el-col>
    <el-col :span="13">
      <el-form v-model="advanceSearchformData" v-if="!show">
        <el-form-item>
          <el-col :span="6">
            <el-select size="large" v-model="advanceSearchformData.fixedSearchData.value1">
              <el-option
                v-for="item in advanceSearchformData.fixedSearchData.options1"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              />
            </el-select>
          </el-col>
          <el-col :span="6">
            <el-select size="large" v-model="advanceSearchformData.fixedSearchData.value2">
              <el-option
                v-for="item in advanceSearchformData.fixedSearchData.options2"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              />
            </el-select>
          </el-col>
          <el-col :span="6">
            <el-input size="large" v-model="advanceSearchformData.fixedSearchData.searchInput"/>
          </el-col>
          <el-col :span="2">
            <el-button size="large" @click="addRow()">
              添加
            </el-button>
          </el-col>
          <el-col :span="3">
            <el-button size="large" @click="advancedRetrieval()" type="primary">
              检索
            </el-button>
          </el-col>
        </el-form-item>
        <el-form-item v-for="(dynamicItem,index) in advanceSearchformData.dynamicSearchData" :key="index">
          <el-col :span="3">
            <el-select size="large" v-model="dynamicItem.value3">
              <el-option
                v-for="item in dynamicItem.options3"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              />
            </el-select>
          </el-col>
          <el-col :span="6">
            <el-select size="large" v-model="dynamicItem.value1">
              <el-option
                v-for="item in dynamicItem.options1"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              />
            </el-select>
          </el-col>
          <el-col :span="4">
            <el-select size="large" v-model="dynamicItem.value2">
              <el-option
                v-for="item in dynamicItem.options2"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              />
            </el-select>
          </el-col>
          <el-col :span="5">
            <el-input size="large" v-model="dynamicItem.searchInput"/>
          </el-col>
          <el-col :span="3">
            <el-button size="large" @click="addRow()">
              添加
            </el-button>
          </el-col>
          <el-col :span="3">
            <el-button size="large" @click="deleteRow(index)">
              删除
            </el-button>
          </el-col>
        </el-form-item>
      </el-form>
      <el-input v-show="show" v-model="normalSearchData.searchInput" @keyup.enter="search" size="large"></el-input>
    </el-col>
    <el-col :span="1">
      <el-button v-show="show" @click="search" size="large">搜索</el-button>
    </el-col>
    <el-col :span="1">
      <el-button @click="show = !show" size="large">高级搜索</el-button>
    </el-col>
  </el-row>
  <el-row>
    <el-col :span="3">
      <RetrievalAside ref="child" :tableData="visibleTableData" @asideFilter="asideFilter"/>
    </el-col>
    <el-col :span="21">
      <el-tabs v-model="tab" class="demo-tabs" @tab-click="switchVisibleBook">
        <el-tab-pane label="所有书籍" name="all"></el-tab-pane>
        <el-tab-pane label="可借书籍" name="available"></el-tab-pane>
      </el-tabs>
      <el-table @row-click="goToDetail" :data="visibleTableData.slice((currentPage-1)*pageSize,currentPage*pageSize)" stripe border style="width: 100%">
        <el-table-column>
          <template #default="scope">
            <el-row>
              <el-col :span="5">
                <div style="font-weight:bold;font-size:20px">{{scope.row.bookname}}</div>
                <div style="font-size:15px">{{scope.row.author}}</div>
                <div style="font-size:15px">{{scope.row.press}}</div>
              </el-col>
              <el-col :span="3" :push="17">
                <div>馆藏数量：{{scope.row.holdingnum}}</div>
                <div>可借数量：{{scope.row.remain}}</div>
              </el-col>
            </el-row>   
          </template>
        </el-table-column>
      </el-table>
    <el-pagination
        v-model:current-page="currentPage"
        :page-sizes="[5, 10, 20]"
        v-model:page-size="pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange">
    </el-pagination>
    </el-col>    
  </el-row>
</template>

<script>
import Header from "@/components/Header";
import axios from 'axios';
import qs from 'qs';
import RetrievalAside from "@/components/RetrievalAside.vue";
export default {
  name: 'HomeView',
  components: {
    Header,
    RetrievalAside,
},
  props:["hello"],
  data() {
    return {
      tableData:[],
      visibleTableData:[],
      advanceSearchformData:{
        fixedSearchData:{
          value1:1,
          value2:1,
          searchInput:'',
          options1:[
            {
              value: 1,
              label: '题名',
            },
            {
              value: 2,
              label: '作者',
            },
            {
              value: 3,
              label: '中图分类号',
            },
            {
              value: 4,
              label: '出版社',
            },
            {
              value: 5,
              label: '图书主题',
            },
          ],
          options2:[
            {
              value: 1,
              label: '包括',
            },
            {
              value: 2,
              label: '精确',
            },
          ],
        },
        dynamicSearchData:[],
      },
      normalSearchData:{
        value:1,
        searchInput:'',
        options:[
          {
            value: 1,
            label: '题名',
          },
          {
            value: 2,
            label: '作者',
          },
          {
            value: 3,
            label: '中图分类号',
          },
          {
            value: 4,
            label: '出版社',
          },
          {
            value: 5,
            label: '图书主题',
          },
        ]
      },
      tab:"all",
      defaultvalue:undefined,
      currentPage:1,
      total: 20, 
      pageSize: 20, 
      show:undefined,
    }
  },
  methods:{
    asideFilter(value){
      if(value.type == "作者"){
        this.visibleTableData = this.visibleTableData.filter(element=>{
          if(element.author == value.keyword){
            return true;
          }
        })
      }
      else if(value.type == "出版社"){
        this.visibleTableData = this.visibleTableData.filter(element=>{
          if(element.press == value.keyword){
            return true;
          }
        })
      }
      else if(value.type == "主题"){
        this.visibleTableData = this.visibleTableData.filter(element=>{
          if(element.category == value.keyword){
            return true;
          }
        })
      }
      else if(value.type == "语言"){
        this.visibleTableData = this.visibleTableData.filter(element=>{
          if(element.language == value.keyword){
            return true;
          }
        })
      }
    },
    switchVisibleBook(){
      if(this.tab == "all")
        this.visibleTableData = this.tableData;
      else
        this.visibleTableData = this.tableData.filter(data=>{
          if(data.remain > 0)
            return true;
        })
    },
    addRow(){
      this.advanceSearchformData.dynamicSearchData.push({
        value1:1,
        value2:1,
        value3:1,
        searchInput:'',
        options1:[
          {
            value: 1,
            label: '题名',
          },
          {
            value: 2,
            label: '作者',
          },
          {
            value: 3,
            label: '中图分类号',
          },
          {
            value: 4,
            label: '出版社',
          },
          {
            value: 5,
            label: '图书主题',
          },
        ],
        options2:[
          {
            value: 1,
            label: '包含',
          },
          {
            value: 2,
            label: '精确',
          },
        ],
        options3:[
          {
            value: 1,
            label: '和',
          },
          {
            value: 2,
            label: '或',
          },
          {
            value: 3,
            label: '否',
          },
        ],
      })
    },
    deleteRow(index){
      this.advanceSearchformData.dynamicSearchData.splice(index,1);
    },
    goToDetail(row){
      window.open("https://localhost/bookDetail?keyword="+encodeURI(row.bookid));
    },
    async search(){
      if(this.normalSearchData.searchInput != "")
      {
        await axios.post('https://localhost/book').then(async res=>{
          this.tableData=res.data
          let searchResult = this.tableData.filter(data=>{
            if(this.normalSearchData.value == 1)
            {
              if(data.bookname.match(this.normalSearchData.searchInput) != null)
              return true;
            }
            else if(this.normalSearchData.value == 2)
            {
              if(data.author.match(this.normalSearchData.searchInput) != null)
              return true;
            }
            else if(this.normalSearchData.value == 3)
            {
              if(data.clcnum.match(this.normalSearchData.searchInput) != null)
              return true;
            }
            else if(this.normalSearchData.value == 4)
            {
              if(data.press.match(this.normalSearchData.searchInput) != null)
              return true;
            }
            else if(this.normalSearchData.value == 5)
            {
              if(data.category.match(this.normalSearchData.searchInput) != null)
              return true;
            }
          })
          this.tableData = searchResult;
          var idList = []
          for(var i = 0;i < searchResult.length;i++)
            idList[i] = searchResult[i].bookid;
          await axios.post('https://localhost/holdingInformation/getHoldingnumAndRemainByBookid',qs.stringify({bookid:idList,length:idList.length})).then(res=>{
            for(var i = 0;i < searchResult.length;i++)
            {
              this.tableData[i]["holdingnum"] = res.data[i].holdingnum;
              this.tableData[i]["remain"] = res.data[i].remain;
            } 
          })
        })
      }
      else
        this.tableData = [];
      this.switchVisibleBook();
    },
    async advancedRetrieval(){
      if(this.advanceSearchformData.fixedSearchData.searchInput != '')
      {
        await axios.post("https://localhost/book").then(async res=>{
          this.tableData = res.data;
          //先固定栏过滤
          this.tableData = this.tableData.filter(data=>{
            if(this.advanceSearchformData.fixedSearchData.value2 == 1)
            {
              if(this.advanceSearchformData.fixedSearchData.value1 == 1)
              {
                if(data.bookname.match(this.advanceSearchformData.fixedSearchData.searchInput) != null)
                  return true;
              }
              else if(this.advanceSearchformData.fixedSearchData.value1 == 2)
              {
                if(data.author.match(this.advanceSearchformData.fixedSearchData.searchInput) != null)
                  return true;
              }
              else if(this.advanceSearchformData.fixedSearchData.value1 == 3)
              {
                if(data.clcnum.match(this.advanceSearchformData.fixedSearchData.searchInput) != null)
                  return true;
              }
              else if(this.advanceSearchformData.fixedSearchData.value1 == 4)
              {
                if(data.press.match(this.advanceSearchformData.fixedSearchData.searchInput) != null)
                  return true;
              }
                else if(this.advanceSearchformData.fixedSearchData.value1 == 5)
              {
                if(data.category.match(this.advanceSearchformData.fixedSearchData.searchInput) != null)
                  return true;
              }
            }
            else if(this.advanceSearchformData.fixedSearchData.value2 == 2)
            {
              if(this.advanceSearchformData.fixedSearchData.value1 == 1)
              {
                if(data.bookname == this.advanceSearchformData.fixedSearchData.searchInput)
                  return true;
              }
              else if(this.advanceSearchformData.fixedSearchData.value1 == 2)
              {
                if(data.author == this.advanceSearchformData.fixedSearchData.searchInput)
                  return true;
              }
              else if(this.advanceSearchformData.fixedSearchData.value1 == 3)
              {
                if(data.clcnum == this.advanceSearchformData.fixedSearchData.searchInput)
                  return true;
              }
              else if(this.advanceSearchformData.fixedSearchData.value1 == 4)
              {
                if(data.press == this.advanceSearchformData.fixedSearchData.searchInput)
                  return true;
              }
                else if(this.advanceSearchformData.fixedSearchData.value1 == 5)
              {
                if(data.category == this.advanceSearchformData.fixedSearchData.searchInput)
                  return true;
              }
            }
          })
          //后动态框过滤
          for(let i = 0;i < this.advanceSearchformData.dynamicSearchData.length;i++){
            if(this.advanceSearchformData.dynamicSearchData[i].searchInput != '')
            {
              //和
              if(this.advanceSearchformData.dynamicSearchData[i].value3 == 1){
                this.tableData = this.tableData.filter(data=>{
                  if(this.advanceSearchformData.dynamicSearchData[i].value2 == 1)
                  {
                    if(this.advanceSearchformData.dynamicSearchData[i].value1 == 1)
                    {
                      if(data.bookname.match(this.advanceSearchformData.dynamicSearchData[i].searchInput) != null)
                        return true;
                    }
                    else if(this.advanceSearchformData.dynamicSearchData[i].value1 == 2)
                    {
                      if(data.author.match(this.advanceSearchformData.dynamicSearchData[i].searchInput) != null)
                        return true;
                    }
                    else if(this.advanceSearchformData.dynamicSearchData[i].value1 == 3)
                    {
                      if(data.clcnum.match(this.advanceSearchformData.dynamicSearchData[i].searchInput) != null)
                        return true;
                    }
                    else if(this.advanceSearchformData.dynamicSearchData[i].value1 == 4)
                    {
                      if(data.press.match(this.advanceSearchformData.dynamicSearchData[i].searchInput) != null)
                        return true;
                    }
                      else if(this.advanceSearchformData.dynamicSearchData[i].value1 == 5)
                    {
                      if(data.category.match(this.advanceSearchformData.dynamicSearchData[i].searchInput) != null)
                        return true;
                    }
                  }
                  else if(this.advanceSearchformData.dynamicSearchData[i].value2 == 2)
                  {
                    if(this.advanceSearchformData.dynamicSearchData[i].value1 == 1)
                    {
                      if(data.bookname == this.advanceSearchformData.dynamicSearchData[i].searchInput)
                        return true;
                    }
                    else if(this.advanceSearchformData.dynamicSearchData[i].value1 == 2)
                    {
                      if(data.author == this.advanceSearchformData.dynamicSearchData[i].searchInput)
                        return true;
                    }
                    else if(this.advanceSearchformData.dynamicSearchData[i].value1 == 3)
                    {
                      if(data.clcnum == this.advanceSearchformData.dynamicSearchData[i].searchInput)
                        return true;
                    }
                    else if(this.advanceSearchformData.dynamicSearchData[i].value1 == 4)
                    {
                      if(data.press == this.advanceSearchformData.dynamicSearchData[i].searchInput)
                        return true;
                    }
                      else if(this.advanceSearchformData.dynamicSearchData[i].value1 == 5)
                    {
                      if(data.category == this.advanceSearchformData.dynamicSearchData[i].searchInput)
                        return true;
                    }
                  }
                })
              }
              //或
              else if(this.advanceSearchformData.dynamicSearchData[i].value3 == 2)
              {
                await axios.post("https://localhost/book").then(res=>{
                  let temp = res.data.filter(data=>{
                    if(this.advanceSearchformData.dynamicSearchData[i].value2 == 1)
                    {
                      if(this.advanceSearchformData.dynamicSearchData[i].value1 == 1)
                      {
                        if(data.bookname.match(this.advanceSearchformData.dynamicSearchData[i].searchInput) != null)
                          return true;
                      }
                      else if(this.advanceSearchformData.dynamicSearchData[i].value1 == 2)
                      {
                        if(data.author.match(this.advanceSearchformData.dynamicSearchData[i].searchInput) != null)
                          return true;
                      }
                      else if(this.advanceSearchformData.dynamicSearchData[i].value1 == 3)
                      {
                        if(data.clcnum.match(this.advanceSearchformData.dynamicSearchData[i].searchInput) != null)
                          return true;
                      }
                      else if(this.advanceSearchformData.dynamicSearchData[i].value1 == 4)
                      {
                        if(data.press.match(this.advanceSearchformData.dynamicSearchData[i].searchInput) != null)
                          return true;
                      }
                        else if(this.advanceSearchformData.dynamicSearchData[i].value1 == 5)
                      {
                        if(data.category.match(this.advanceSearchformData.dynamicSearchData[i].searchInput) != null)
                          return true;
                      }
                    }
                    else if(this.advanceSearchformData.dynamicSearchData[i].value2 == 2)
                    {
                      if(this.advanceSearchformData.dynamicSearchData[i].value1 == 1)
                      {
                        if(data.bookname == this.advanceSearchformData.dynamicSearchData[i].searchInput)
                          return true;
                      }
                      else if(this.advanceSearchformData.dynamicSearchData[i].value1 == 2)
                      {
                        if(data.author == this.advanceSearchformData.dynamicSearchData[i].searchInput)
                          return true;
                      }
                      else if(this.advanceSearchformData.dynamicSearchData[i].value1 == 3)
                      {
                        if(data.clcnum == this.advanceSearchformData.dynamicSearchData[i].searchInput)
                          return true;
                      }
                      else if(this.advanceSearchformData.dynamicSearchData[i].value1 == 4)
                      {
                        if(data.press == this.advanceSearchformData.dynamicSearchData[i].searchInput)
                          return true;
                      }
                        else if(this.advanceSearchformData.dynamicSearchData[i].value1 == 5)
                      {
                        if(data.category == this.advanceSearchformData.dynamicSearchData[i].searchInput)
                          return true;
                      }
                    }
                  })
                  this.tableData = this.tableData.concat(temp);
                })
              }
              //非
              else if(this.advanceSearchformData.dynamicSearchData[i].value3 == 3)
              {
                this.tableData = this.tableData.filter(data=>{
                  if(this.advanceSearchformData.dynamicSearchData[i].value2 == 1)
                  {
                    if(this.advanceSearchformData.dynamicSearchData[i].value1 == 1)
                    {
                      if(data.bookname.match(this.advanceSearchformData.dynamicSearchData[i].searchInput) == null)
                        return true;
                    }
                    else if(this.advanceSearchformData.dynamicSearchData[i].value1 == 2)
                    {
                      if(data.author.match(this.advanceSearchformData.dynamicSearchData[i].searchInput) == null)
                        return true;
                    }
                    else if(this.advanceSearchformData.dynamicSearchData[i].value1 == 3)
                    {
                      if(data.clcnum.match(this.advanceSearchformData.dynamicSearchData[i].searchInput) == null)
                        return true;
                    }
                    else if(this.advanceSearchformData.dynamicSearchData[i].value1 == 4)
                    {
                      if(data.press.match(this.advanceSearchformData.dynamicSearchData[i].searchInput) == null)
                        return true;
                    }
                      else if(this.advanceSearchformData.dynamicSearchData[i].value1 == 5)
                    {
                      if(data.category.match(this.advanceSearchformData.dynamicSearchData[i].searchInput) == null)
                        return true;
                    }
                  }
                  else if(this.advanceSearchformData.dynamicSearchData[i].value2 == 2)
                  {
                    if(this.advanceSearchformData.dynamicSearchData[i].value1 == 1)
                    {
                      if(data.bookname != this.advanceSearchformData.dynamicSearchData[i].searchInput)
                        return true;
                    }
                    else if(this.advanceSearchformData.dynamicSearchData[i].value1 == 2)
                    {
                      if(data.author != this.advanceSearchformData.dynamicSearchData[i].searchInput)
                        return true;
                    }
                    else if(this.advanceSearchformData.dynamicSearchData[i].value1 == 3)
                    {
                      if(data.clcnum != this.advanceSearchformData.dynamicSearchData[i].searchInput)
                        return true;
                    }
                    else if(this.advanceSearchformData.dynamicSearchData[i].value1 == 4)
                    {
                      if(data.press != this.advanceSearchformData.dynamicSearchData[i].searchInput)
                        return true;
                    }
                      else if(this.advanceSearchformData.dynamicSearchData[i].value1 == 5)
                    {
                      if(data.category != this.advanceSearchformData.dynamicSearchData[i].searchInput)
                        return true;
                    }
                  }
                })
              }
            }
          }
        }) 
      }
      else
        this.tableData = [];
      var idList = []
          for(var i = 0;i < this.tableData.length;i++)
            idList[i] = this.tableData[i].bookid;
      await axios.post('https://localhost/holdingInformation/getHoldingnumAndRemainByBookid',qs.stringify({bookid:idList,length:idList.length})).then(res=>{
        for(var i = 0;i < this.tableData.length;i++)
        {
          this.tableData[i]["holdingnum"] = res.data[i].holdingnum;
          this.tableData[i]["remain"] = res.data[i].remain;
        } 
      })
      this.visibleTableData = this.tableData;
    }
  },
  created(){
  this.normalSearchData.searchInput = decodeURI(window.location.search.slice(9));
  this.search();
  this.defaultvalue = 2;
  this.show = true;
  }
}
</script>