<template>
  <el-col :span="2">
    <span>搜索</span>
  </el-col>
  <el-col :span="2" :offset="20">
    <el-dropdown>
        <font color="green">{{username}}</font>
        <template #dropdown>
          <el-dropdown-menu>
            <el-dropdown-item>注销</el-dropdown-item>
          </el-dropdown-menu>
        </template>
      </el-dropdown>
  </el-col>     
</template>

<script>
import DropDown from "@/components/DropDown";
import axios from 'axios';
import qs from 'qs'
export default {
  name: "Header",
  components:{
    DropDown
  },
  data() {
    return {
      username:undefined, 
    }
  },
  methods:{
    OpenPersonalInfo(){
      axios.post('https://localhost/server/url').then(res=>{
        window.open(res.data+"/personalCenter");
      })
    }
  },
  created(){
    let token = JSON.parse(localStorage.getItem('token'));
    let id = token.id;
    axios.post('https://localhost/user/getUser',qs.stringify({id:id})).then(res=>{
      this.username = res.data.username;
    })
  }
}
</script>

