<template>
  <div style="display: flex">
    <Aside @CLCSearch="CLCSearch"/>
    <el-table @row-click="goToDetail" :data="tableData.slice((currentPage-1)*pageSize,currentPage*pageSize)" stripe border style="width: 100%">
      <el-table-column>
        <template #default="scope">
          <el-row>
            <el-col :span="5">
              <div style="font-weight:bold;font-size:20px">{{scope.row.bookname}}</div>
              <div style="font-size:15px">{{scope.row.author}}</div>
              <div style="font-size:15px">{{scope.row.press}}</div>
            </el-col>
            <el-col :span="3" :push="17">
              <div>馆藏数量：{{scope.row.holdingnum}}</div>
              <div>可借数量：{{scope.row.remain}}</div>
            </el-col>
          </el-row>   
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
import Header from "@/components/Header";
import Aside from "@/components/Aside";
import axios from 'axios';
import qs from "qs";
export default {
  name: 'HomeView',
  components: {
    Header,
    Aside
  },
  data() {
    return {
      tableData:[],
      options:[
  {
    value: 1,
    label: '一个月',
  },
  {
    value: 2,
    label: '2个月',
  },
  {
    value: 3,
    label: '3个月',
  },
],
      searchInput:undefined,
      currentPage:1,
      total: 20, 
      pageSize: 20, 
    }
  },
  methods:{
    goToDetail(row){
      window.open("https://localhost/bookDetail?keyword="+encodeURI(row.bookid));
    },
    change(){
      this.$forceUpdate()
    },
    search(){
      axios.post('https://localhost/book').then(res=>{
      this.tableData=res.data
      let searchResult = this.tableData.filter(data=>{
         if(data.bookid == this.searchInput || data.bookname == this.searchInput)
          return true;
        })
      this.tableData = searchResult;
      })
    },
    CLCSearch(CLCnum)
    {
      axios.post('https://localhost/book').then(res=>{
        this.tableData = res.data;
        this.tableData = this.tableData.filter(data=>{
            if(data.clcnum != null && data.clcnum.match(CLCnum) != null)
              return true;
          })
          var idList = []
          for(var i = 0;i < this.tableData.length;i++)
            idList[i] = this.tableData[i].bookid;
          axios.post('https://localhost/holdingInformation/getHoldingnumAndRemainByBookid',qs.stringify({bookid:idList,length:idList.length})).then(res=>{
            for(var i = 0;i < this.tableData.length;i++)
            {
              this.tableData[i]["holdingnum"] = res.data[i].holdingnum;
              this.tableData[i]["remain"] = res.data[i].remain;
            } 
          })
      })
    },
  },
}
</script>
